'use strict';

(function () {
  let slider = document.querySelector('.countries-information__slide-list');
  let control = document.querySelector('.countries-information__control-list');
  let mySwiper;

  const DESKTOP_WIDTH = 1213;

  if (window.innerWidth < DESKTOP_WIDTH) {
    mySwiper = new Swiper('.swiper-container', {
      speed: 400,
    });
  }


  window.addEventListener('resize', function () {
    if (window.innerWidth < DESKTOP_WIDTH) {
      if (!mySwiper) {
        mySwiper = new Swiper('.swiper-container', {
          speed: 400,
          init: true
        })

      }
    } else {
      if (mySwiper) {
        mySwiper.destroy();
      }
      slider.removeAttribute('style');
      control.removeAttribute('style')
    }
  });
})();
